















import {Component, Vue, Watch} from 'vue-property-decorator';
import {NavBar, Icon, Divider} from 'vant';
import 'vant/lib/nav-bar/style';
import 'vant/lib/icon/style';
import 'vant/lib/divider/style';

Vue.use(NavBar).use(Icon).use(Divider);
@Component
export default class Frame extends Vue {
    private SkipSwitchName = "";

    public onClickLeft(): void {
        this.$router.go(-1);
    }

    @Watch('$route', {immediate: true, deep: true})
    public Change(toRouter: any, fromRouter: any) {
        if (toRouter === null || fromRouter === null || fromRouter === undefined || toRouter === undefined ||
            fromRouter["meta"].tx === undefined || toRouter["meta"].tx === undefined) {
        } else {
            console.log("toRouter===>" + toRouter["meta"].tx + "||" + fromRouter["meta"].tx);
            if (toRouter["meta"].tx > fromRouter["meta"].tx) {
                this.SkipSwitchName = "Skright";
            } else {
                this.SkipSwitchName = "Skleft";
            }
        }
    }
}
